@charset "UTF-8";
:root {
  --corporate-v-0: #161D31;
  --corporate-v-1: #34495E;
  --corporate-v-2: #337AB7;
  --corporate-v-3: #039BB3;
  --color-gray-100: #F8F9FA;
  --color-gray-200: #E9ECEF;
  --color-gray-300: #DEE2E6;
  --color-gray-400: #CED4DA;
  --color-gray-500: #ADB5BD;
  --color-gray-600: #6C757D;
  --color-gray-700: #495057;
  --color-gray-800: #343A40;
  --color-gray-900: #212529;
  --color-status-danger: #CC3333;
  --color-status-succes: #009933;
}

.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

@font-face {
  font-family: "Nunito";
  src: url("../fonts/../fonts/Nunitoextralight.woff2") format("woff2");
  font-weight: 200;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Nunito";
  src: url("../fonts/../fonts/Nunitolight.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Nunito";
  src: url("../fonts/../fonts/Nunitoregular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Nunito";
  src: url("../fonts/../fonts/Nunitosemibold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Nunito";
  src: url("../fonts/../fonts/Nunitobold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Nunito";
  src: url("../fonts/../fonts/Nunitoextrabold.woff2") format("woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Nunito";
  src: url("../fonts/../fonts/Nunitobold.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

:focus {
  outline: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Nunito", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: var(--color-gray-900);
  padding-left: env(safe-area-inset-left);
  padding-bottom: env(safe-area-inset-bottom);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

p {
  font-size: 16px;
  line-height: 22px;
  margin-top: 0;
}
p:not(:last-child) {
  margin-bottom: 15px;
}

svg {
  width: 20px;
  height: 20px;
}

.site-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100vh;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 16px;
  max-width: 1440px;
  width: 100%;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.transition {
  transition: all 0.2s;
}
.transition:hover, .transition:focus {
  transition: all 0.2s;
}

.text__block ul {
  margin: 0 0 25px 0;
  padding: 0 0 0 15px;
  list-style: none;
}
.text__block ul li {
  padding-left: 15px;
  position: relative;
  font-size: 16px;
  line-height: 22px;
}
.text__block ul li::before {
  content: "";
  width: 5px;
  height: 5px;
  position: absolute;
  left: 0;
  display: inline-block;
  background-color: var(--corporate-v-2);
  border-radius: 50%;
  top: 9px;
}
.text__block ul li:not(:last-child) {
  margin-bottom: 5px;
}

.main__indent {
  display: block;
  width: 100%;
  height: 130px;
  position: relative;
  z-index: -1;
}
@media screen and (max-width: 1919px) {
  .main__indent {
    height: 80px;
  }
}

.error_page {
  display: flex;
  justify-content: center;
  width: 100%;
}
.error_page svg {
  width: 688px;
  height: 433px;
}
@media screen and (max-width: 1023px) {
  .error_page svg {
    width: 508px;
    height: 253px;
  }
}
@media screen and (max-width: 767px) {
  .error_page svg {
    width: auto;
    height: auto;
  }
}

.main__button {
  background-color: var(--corporate-v-2);
  border-radius: 4px;
  border: 2px solid var(--corporate-v-2);
  padding: 10px 25px;
  font-size: 16px;
  font-weight: 700;
  color: var(--color-gray-100);
  transition: all 0.2s;
  white-space: nowrap;
}
.main__button:hover, .main__button:focus, .main__button:active, .main__button.active {
  background-color: var(--color-gray-100);
  color: var(--corporate-v-2);
  transition: all 0.2s;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.footnote {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;
  border-top: 1px solid var(--color-gray-200);
  margin-top: 70px;
}
@media screen and (max-width: 767px) {
  .footnote {
    margin-top: 50px;
    flex-direction: column;
    align-items: flex-start;
  }
}
.footnote__content {
  color: var(--corporate-v-0);
  font-size: 16px;
  line-height: 22px;
  padding-right: 20px;
}
@media screen and (max-width: 767px) {
  .footnote__content {
    margin-bottom: 20px;
    padding-right: 0;
  }
}
.footnote__content a {
  color: var(--corporate-v-2);
}
.footnote__title {
  font-weight: 700;
  display: block;
  margin-bottom: 5px;
}

.main__form {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: 1fr 1fr 1fr;
}
@media screen and (max-width: 767px) {
  .main__form {
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }
}
.main__form-text {
  margin: 0 0 0 25px;
  font-size: 14px;
  color: var(--corporate-v-0);
  line-height: 16px;
}
@media screen and (max-width: 767px) {
  .main__form-text {
    text-align: center;
    margin: 15px 0 0 0;
  }
}
.main__form-link {
  color: var(--corporate-v-2);
}
.main__form-link:hover, .main__form-link:focus {
  text-decoration: underline;
  color: var(--corporate-v-3);
}
@media screen and (min-width: 768px) {
  .main__form-link {
    display: table;
  }
}
.main__form-field {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.main__form-field--full {
  grid-column-start: 1;
  grid-column-end: 4;
}
@media screen and (max-width: 767px) {
  .main__form-field--full {
    grid-column-end: 1;
  }
}
.main__form-field--buttons {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-top: 10px;
}
@media screen and (max-width: 767px) {
  .main__form-field--buttons {
    flex-direction: column;
  }
}
.main__form-label {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  color: var(--corporate-v-1);
  margin-bottom: 10px;
}
@media screen and (max-width: 1279px) {
  .main__form-label {
    font-size: 14px;
    line-height: 16px;
  }
}
.main__form-input, .main__form-files {
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  padding: 0 15px;
  height: 48px;
  line-height: 48px;
  color: var(--color-gray-600);
  box-shadow: 0px 2px 6px rgba(52, 73, 94, 0.15);
  text-overflow: ellipsis;
  font-size: 14px;
  position: relative;
  width: 100%;
  overflow: hidden;
  max-width: 500px;
  text-overflow: ellipsis;
}
.main__form-input::-webkit-input-placeholder, .main__form-files::-webkit-input-placeholder {
  color: var(--color-gray-600);
}
.main__form-input:-moz-placeholder, .main__form-files:-moz-placeholder {
  color: var(--color-gray-600);
}
.main__form-input::-moz-placeholder, .main__form-files::-moz-placeholder {
  color: var(--color-gray-600);
}
.main__form-input:-ms-input-placeholder, .main__form-files:-ms-input-placeholder {
  color: var(--color-gray-600);
}
.main__form-input:hover::placeholder, .main__form-files:hover::placeholder {
  opacity: 1;
}
.main__form-input:focus, .main__form-files:focus {
  border: 1px solid var(--color-gray-300);
}
.main__form-input:focus::placeholder, .main__form-files:focus::placeholder {
  opacity: 0.3;
}
.main__form-input--file, .main__form-files--file {
  width: 100%;
}
.main__form-files {
  padding: 0;
  background-image: url(../img/icon__files.svg);
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: 10px 50%;
}
.main__form .field-file__input {
  position: absolute;
  opacity: 0;
  top: 0;
  right: 100%;
}
.main__form .field-file__label {
  padding: 0 40px;
  width: 100%;
  height: 100%;
  display: block;
  cursor: pointer;
}
.main__form .field-file__label--text {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  display: block;
}
.main__form-textarea {
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  padding: 15px;
  height: auto;
  line-height: 20px;
  min-height: 190px;
  max-height: 190px;
  min-width: 100%;
  max-width: 100%;
  width: 100%;
  color: var(--color-gray-600);
  box-shadow: 0px 2px 6px rgba(52, 73, 94, 0.15);
}
.main__form-textarea::-webkit-input-placeholder {
  color: var(--color-gray-600);
}
.main__form-textarea:-moz-placeholder {
  color: var(--color-gray-600);
}
.main__form-textarea::-moz-placeholder {
  color: var(--color-gray-600);
}
.main__form-textarea:-ms-input-placeholder {
  color: var(--color-gray-600);
}
.main__form-textarea:hover::placeholder {
  opacity: 1;
}
.main__form-textarea:focus {
  border: 1px solid var(--color-gray-300);
}
.main__form-textarea:focus::placeholder {
  opacity: 0.3;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.tags__item {
  background-color: var(--color-gray-400);
  border-radius: 4px;
  padding: 8px 12px;
  font-size: 12px;
  line-height: 16px;
  color: var(--corporate-v-1);
  margin: 0 5px 5px 0;
}
.tags__item:hover {
  color: var(--color-gray-100);
}

.slaider__list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  width: 100%;
}
@media screen and (max-width: 1023px) {
  .slaider__list {
    display: none;
  }
}
.slaider__list-item {
  width: 20px;
  height: 5px;
  border-radius: 10px;
  background-color: var(--corporate-v-1);
  cursor: pointer;
}
.slaider__list-item:not(:last-child) {
  margin-right: 5px;
}
.slaider__list-item--active {
  width: 40px;
  background-color: var(--corporate-v-2);
}

details {
  position: relative;
  min-height: auto;
  max-height: 100%;
  transition: min-height 1.15s linear, max-height 1.5s linear;
  -webkit-transition: min-height 1.15s linear, max-height 1.5s linear;
  will-change: max-height;
  width: 100%;
}
details summary {
  display: flex;
  cursor: pointer;
  outline: 0;
  align-items: center;
  position: relative;
  /*&::before {
        content: "";
        transition: transform .1s linear;
        -webkit-transition: transform .1s linear;
        background-image: url(../../../../assets/media/arrow.svg);
        background-repeat: no-repeat;
        background-size: 10px 6px;
        margin-left: 15px;
        width: 10px;
        height: 6px;
        order: 3;
    }*/
}
details summary::-webkit-details-marker {
  display: none;
}
details summary::-moz-list-bullet {
  list-style-type: none;
}
details[open] {
  min-height: auto;
  max-height: 100%;
}
details[open] summary ~ * {
  opacity: 1;
}
details[open] summary:before {
  transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
  -moz-transform: rotate(-180deg);
}

/*
details[open] summary ~ * {
  animation: sweep .5s ease-in-out;
}

@keyframes sweep {
  0%    {opacity: 0; margin-left: -10px}
  100%  {opacity: 1; margin-left: 0px}
}*/
.modal-overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}

.modal-overlay--visible {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease-in-out;
}

.modal--visible {
  display: block;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__top {
  background-color: #DEE2E6;
  box-shadow: 0px 1px 4px rgba(51, 51, 51, 0.05);
  width: 100%;
  padding: 11px 0;
  display: flex;
  align-items: center;
}
.header__list {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .header__list {
    width: 100%;
    justify-content: space-between;
  }
}
.header__list-link {
  font-size: 15px;
  font-weight: 600;
  color: var(--corporate-v-0);
  display: flex;
  align-items: center;
  transition: all 0.2s;
}
.header__list-link svg {
  fill: var(--corporate-v-0);
  margin-right: 5px;
  transition: all 0.2s;
}
.header__list-link:hover, .header__list-link:focus {
  color: var(--corporate-v-2);
  transition: all 0.2s;
}
.header__list-link:hover svg, .header__list-link:focus svg {
  fill: var(--corporate-v-2);
  transition: all 0.2s;
}
.header__list-item {
  display: flex;
  align-items: center;
}
.header__list-item:not(:last-child) {
  margin-right: 20px;
}
.header__list-modal {
  margin-left: 10px;
  font-size: 13px;
  font-weight: 300;
  cursor: pointer;
  color: var(--corporate-v-0);
  transition: all 0.2s;
  position: relative;
  text-decoration: underline;
  /*&::after {
    content: '';
    position: absolute;
    bottom: 0;
    height: 1px;
    background-color: var(--corporate-v-0);
    width: 100%;
    display: block;
  }*/
}
.header__list-modal:hover, .header__list-modal:focus {
  color: var(--corporate-v-2);
  transition: all 0.2s;
}
@media screen and (max-width: 767px) {
  .header__list-modal {
    display: none;
  }
}
.header__social {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .header__social {
    display: none;
  }
}
.header__social svg {
  fill: var(--corporate-v-1);
  margin-right: 5px;
  transition: all 0.2s;
}
.header__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 17px 0;
}
.header__logo-link {
  background: url(../img/logo.svg) no-repeat scroll 0 0;
  padding-top: 40px;
  width: 100%;
  background-size: 210px;
  min-width: 210px;
  min-height: 71px;
  height: 100%;
  display: block;
}
@media screen and (max-width: 767px) {
  .header__logo-link {
    padding-top: 0;
    min-height: 30px;
  }
}
.header__logo span {
  font-size: 14px;
  line-height: 16px;
  color: var(--corporate-v-1);
  font-weight: 600;
}
@media screen and (max-width: 767px) {
  .header__logo span {
    display: none;
  }
}
.header__nav {
  margin-left: auto;
  margin-right: 50px;
}
@media screen and (max-width: 1279px) {
  .header__nav {
    margin-right: 30px;
  }
}
.header__menu {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 1023px) {
  .header__menu {
    display: none;
  }
}
.header__menu-item:not(:last-child) {
  margin-right: 25px;
}
.header__menu-link {
  color: var(--corporate-v-1);
  font-size: 18px;
  font-weight: 700;
  transition: all 0.2s;
}
.header__menu-link:hover, .header__menu-link:focus {
  color: var(--corporate-v-2);
  transition: all 0.2s;
}
.header__search, .header__adaptive {
  width: 35px;
  height: 35px;
  cursor: pointer;
  display: flex;
  flex-shrink: 0;
  background: none;
  border: none;
}
.header__search svg, .header__adaptive svg {
  width: 100%;
  height: 100%;
  fill: var(--corporate-v-1);
  transition: all 0.2s;
}
.header__search:hover svg, .header__search:focus svg, .header__adaptive:hover svg, .header__adaptive:focus svg {
  fill: var(--corporate-v-2);
  transition: all 0.2s;
}
.header__search {
  fill: var(--corporate-v-1);
  transition: all 0.2s;
}
.header__search-button {
  background: none;
  border: none;
}
.header__search:hover, .header__search:focus {
  fill: var(--corporate-v-2);
  transition: all 0.2s;
}
.header__adaptive {
  display: none;
  margin-left: 10px;
}
@media screen and (max-width: 1023px) {
  .header__adaptive {
    display: flex;
  }
}

.search__fixed {
  background-color: #F8F9FB;
  width: 100%;
  padding: 40px 0;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 152px;
  display: none;
}
@media screen and (max-width: 767px) {
  .search__fixed {
    padding: 25px 0;
    top: 110px;
  }
}
.search__fixed-form {
  width: 100%;
  display: flex;
  justify-content: center;
}
.search__fixed-field {
  display: flex;
  justify-content: center;
  width: 100%;
  position: relative;
}
.search__fixed-input {
  background-color: #fff;
  padding: 13px 10px;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  height: 42px;
  max-width: 810px;
  width: 100%;
  font-size: 14px;
  border: 1px solid var(--color-gray-300);
}
.search__fixed-input::-webkit-input-placeholder {
  color: var(--color-gray-600);
}
.search__fixed-input:-moz-placeholder {
  color: var(--color-gray-600);
}
.search__fixed-input::-moz-placeholder {
  color: var(--color-gray-600);
}
.search__fixed-input:-ms-input-placeholder {
  color: var(--color-gray-600);
}
.search__fixed-input:hover::placeholder {
  opacity: 1;
}
.search__fixed-input:focus::placeholder {
  opacity: 0.3;
}
.search__fixed-button {
  max-height: 42px;
}

.breadcrumbs {
  background-color: var(--corporate-v-1);
  padding: 35px 0;
}
@media screen and (max-width: 1023px) {
  .breadcrumbs {
    padding: 25px 0;
  }
}
@media screen and (max-width: 767px) {
  .breadcrumbs {
    padding: 20px 0;
  }
}
.breadcrumbs__title {
  font-weight: 800;
  color: var(--color-gray-100);
  font-size: 26px;
}
@media screen and (max-width: 767px) {
  .breadcrumbs__title {
    font-size: 22px;
  }
}
.breadcrumbs__list {
  margin-top: 5px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.breadcrumbs__item {
  font-size: 14px;
  line-height: 16px;
  text-transform: lowercase;
  color: var(--color-gray-100);
}
@media screen and (max-width: 767px) {
  .breadcrumbs__item {
    font-size: 13px;
  }
}
.breadcrumbs__item:not(:last-child) {
  margin-right: 2px;
}
.breadcrumbs__item:not(:last-child)::after {
  content: "";
  margin-left: 2px;
  text-decoration: none;
}
.breadcrumbs__link {
  text-decoration: underline;
  color: var(--color-gray-100);
