}
.news__main .main__button {
  margin: 50px auto 0;
}
@media screen and (max-width: 767px) {
  .news__main .main__button {
    margin-top: 30px;
  }
}

.retail__all {
  padding: 80px 0;
  background-color: var(--color-gray-100);
}
@media screen and (max-width: 1023px) {
  .retail__all {
    padding: 50px 0;
  }
}
.retail__all-list {
  display: flex;
  overflow: hidden;
  padding-bottom: 5px;
}
@media screen and (max-width: 1023px) {
  .retail__all-list {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    width: 100vw;
    position: relative;
    left: 0;
    padding: 10px 16px 10px 16px;
    scroll-snap-type: x proximity;
    scroll-padding: 0 20px;
    scrollbar-width: none;
    grid-column-gap: 0;
    margin-left: -16px;
    margin-right: -16px;
  }
  .retail__all-list::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
  .retail__all-list:after {
    content: "";
    height: 20px;
    width: 20px;
    flex-shrink: 0;
  }
}
.retail__all-item {
  width: 145px;
  height: 145px;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0px 1px 6px rgba(52, 73, 94, 0.15);
  flex-shrink: 0;
}
.retail__all-item:not(:last-child) {
  margin-right: 35px;
}
@media screen and (max-width: 1023px) {
  .retail__all-item {
    width: 120px;
    height: 120px;
  }
  .retail__all-item:not(:last-child) {
    margin-right: 25px;
  }
}
.retail__all-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.hystmodal__opened {
  position: fixed;
  right: 0;
  left: 0;
  overflow: hidden;
}

.hystmodal__shadow {
  position: fixed;
  border: none;
  display: block;
  width: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: hidden;
  pointer-events: none;
  z-index: 98;
  opacity: 0;
  transition: opacity 0.15s ease;
  background-color: #161D31;
}

.hystmodal__shadow--show {
  pointer-events: auto;
  opacity: 0.4;
}

.hystmodal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  opacity: 1;
  pointer-events: none;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  z-index: 99;
  visibility: hidden;
}

.hystmodal--active {
  opacity: 1;
}

.hystmodal--moved,
.hystmodal--active {
  pointer-events: auto;
  visibility: visible;
}

.hystmodal__wrap {
  flex-shrink: 0;
  flex-grow: 0;
  width: 100%;
  min-height: 100%;
  margin: auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
}

.hystmodal__window {
  margin: 50px 0;
  box-sizing: border-box;
  flex-shrink: 0;
  flex-grow: 0;
  background: var(--color-gray-100);
  width: 100%;
  max-width: 580px;
  overflow: visible;
  opacity: 0;
  border-radius: 10px;
  padding: 40px;
}
@media screen and (max-width: 767px) {
  .hystmodal__window {
    padding: 25px 20px;
    width: calc(100% - 32px);
  }
}

.hystmodal--active .hystmodal__window {
  transform: scale(1);
  opacity: 1;
}

.hystmodal__close {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 5px;
  right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}
@media screen and (max-width: 767px) {
  .hystmodal__close {
    top: 0;
    right: 0;
  }
}
.hystmodal__close svg {
  width: 30px;
  height: 30px;
  min-width: 30px;
  min-height: 30px;
  display: block;
}
.hystmodal__title {
  font-size: 26px;
  line-height: 38px;
  font-weight: 600;
  color: var(--corporate-v-1);
  margin-top: 0;
  margin-bottom: 5px;
}
@media screen and (max-width: 767px) {
  .hystmodal__title {
    font-size: 20px;
    line-height: 28px;
  }
}
.hystmodal__subtitle {
  font-size: 16px;
  line-height: 24px;
  color: var(--corporate-v-1);
}
@media screen and (max-width: 767px) {
  .hystmodal__subtitle {
    font-size: 14px;
    line-height: 20px;
  }
}
.hystmodal__form {
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
}
@media screen and (max-width: 767px) {
  .hystmodal__form {
    margin-top: 30px;
  }
}
.hystmodal .main__form-field:last-child {
  margin-top: 15px;
}
.hystmodal .main__form-text {
  display: flex;
  flex-direction: row;
  margin: 0;
  text-align: left;
}
.hystmodal .main__form-link {
  display: contents;
}
.hystmodal .main__button {
  margin-top: 15px;
  display: table;
  max-width: max-content;
}